var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'2. Metodologías de diagnóstico'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_vm._m(0),_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Pasos para elaborar un diagnóstico empresarial:")]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(1),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Preparación"}},[_c('p',[_vm._v("Al realizar un diagnóstico empresarial, es fundamental que tanto la organización como sus colaboradores, se preparen adecuadamente.")]),_c('h5',[_vm._v("Preparación de la empresa:")]),_c('p',[_vm._v("Para facilitar el trabajo del asesor o del equipo interno encargado, la empresa debe reunir y organizar con anticipación, toda la información relevante que sirva como base para el diagnóstico. Esta información incluye documentos esenciales como balances financieros, estados de resultados, informes de gestión, manuales de funciones y procedimientos, catálogos de productos, actas de reuniones, comunicaciones con clientes, reportes de asesoría, boletines del sector, análisis de la competencia, estadísticas relacionadas y material bibliográfico complementario. Además, contar con un equipo de cómputo actualizado y con el software apropiado, agiliza el proceso y mejora la precisión del análisis.")]),_c('p',[_vm._v("El asesor o el equipo de diagnóstico elabora una propuesta de plan de acción que detalla las actividades principales a desarrollar. Este plan se presenta a la gerencia para su revisión y ajuste y, posteriormente, se estructura en un cronograma de actividades que debe ser comunicado de manera clara y oportuna, a todos los involucrados en el proyecto.")]),_c('h5',[_vm._v("Preparación de los colaboradores:")]),_c('p',[_vm._v("Es esencial programar reuniones con los colaboradores que participarán directamente en el proceso diagnóstico. Durante estos encuentros, es importante explicar los objetivos y beneficios que traerá el diagnóstico, tanto para la empresa como para sus empleados y los clientes. Transmitir la importancia del proceso y destacar las oportunidades de mejora, ayudará a obtener el compromiso y la colaboración activa de todos los participantes. ")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Análisis"}},[_c('p',[_vm._v("Una empresa, por lo general, se organiza en diversas áreas funcionales, entre las que se destacan: administración, mercadeo, recursos humanos, producción y finanzas. Estas áreas no operan de manera aislada, sino que mantienen una estrecha interdependencia, lo que significa que el funcionamiento de una, influye directamente en las demás.")]),_c('p',[_vm._v("Para llevar a cabo un diagnóstico empresarial integral, es indispensable analizar cada una de estas áreas, junto con cualquier otra que resulte relevante, según la estructura y el giro de la empresa. Este análisis detallado permite identificar tanto las fortalezas como las debilidades presentes en cada departamento, lo que resulta esencial para que la organización pueda anticiparse y responder de manera efectiva, a las amenazas externas y aprovechar las oportunidades que surjan en el entorno.")]),_c('p',[_vm._v("Es importante que las conclusiones obtenidas durante el diagnóstico, no se basen únicamente en percepciones subjetivas. Por el contrario, deben sustentarse en información concreta y verificable, como cifras financieras, registros contables, informes de desempeño, estadísticas del mercado y otros datos objetivos que reflejen la realidad operativa de la empresa. De esta manera, se asegura que las decisiones estratégicas que se tomen posteriormente, tengan una base sólida y confiable, facilitando la planificación de acciones correctivas o de mejora. ")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Definición de la situación o necesidad"}},[_c('p',[_vm._v("Después de examinar cada área mediante diversas técnicas y haber identificado sus fortalezas y debilidades, se elabora una lista priorizada. Este proceso permite determinar el problema central, la necesidad o el evento fundamental que afecta a toda la organización. Una vez identificado, se analizan las posibles causas que lo originaron y las repercusiones que tiene en el desarrollo general de la empresa. ")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Plan de acción"}},[_c('p',[_vm._v("Una vez identificado el \"problema central\" de la organización y debatido dentro del equipo de trabajo, el siguiente paso es diseñar un plan de acción. Este plan consiste en establecer una serie de lineamientos o pasos que se llevarán a cabo en plazos cortos, medianos y largos, de acuerdo con las prioridades definidas. Es esencial que el plan sea realista y se ajuste a los recursos disponibles. Además, debe ser claro, práctico y contar con el respaldo firme de la alta dirección para garantizar su implementación efectiva. ")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Redacción del informe de diagnóstico"}},[_c('p',[_vm._v("El informe final del diagnóstico, debe presentarse de manera escrita y complementarse con una exposición oral dirigida a los interesados. Esta presentación tiene como propósito explicar los puntos claves del análisis y aclarar cualquier duda que surja en su interpretación. El documento debe iniciar con una descripción clara de los objetivos planteados, la metodología empleada y los resultados obtenidos, tanto de forma general como desglosada por áreas específicas. Es fundamental resaltar las fortalezas identificadas, proponer las correcciones necesarias y sugerir ajustes, además de incluir mecanismos de control que aseguren la correcta implementación del plan de acción.")]),_c('p',[_vm._v("Este informe debe convertirse en una herramienta clave que facilite la toma de decisiones estratégicas, promoviendo la continuidad, el crecimiento y la rentabilidad de la organización. Asimismo, es esencial que los líderes de cada área y el resto del equipo comprendan la información presentada, especialmente cuando se apoya en gráficos, tablas y esquemas visuales. De esta manera, se podrá dar inicio al plan de forma eficiente y en el momento adecuado. ")])])])],1),_vm._m(2)]),_vm._m(3),_vm._m(4),_c('figcaption',{staticClass:"bg-color-6 mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Nota: tomado y adaptado de Prieto Herrera (2011).")]),_vm._m(5),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-0",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("La validez")]),_c('p',[_vm._v("La validez del diagnóstico radica en su capacidad para reflejar fielmente, la realidad de la empresa, asegurando que el análisis revele la situación auténtica y no una percepción distorsionada de lo que se cree que sucede.")]),_c('p',[_vm._v("Este diagnóstico debe servir como una base para orientar la toma de decisiones, considerando las fortalezas, debilidades, oportunidades y potenciales de la organización; su propósito principal es el de reducir o eliminar la incertidumbre en áreas como la estructura interna, la gestión, la estrategia, la producción, el marketing y los sistemas de información, proporcionando una guía más clara y efectiva para el futuro de la empresa.")])]),_c('div',{staticClass:"col-lg-5 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"img-a img-t",attrs:{"src":require("@/assets/curso/temas/19.png")}})])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-7"},[_c('h5',[_vm._v("La confiabilidad")]),_c('p',[_vm._v("La confiabilidad se 